import { position } from '../../Core';
import { CreateAround, Random, RandomEven } from './Core';

const direction = {
    horizontal: 0,
    vertical: 1
}

function RecursiveDivision(whichAlgo) {
    var walls = CreateAround();
    var weights = [];

    // truefalse
    // 
    switch (whichAlgo) {
        case "RecursiveDivision":
            DoRecursiveDivision(0, 0, position.colSize, position.rowSize, walls);
            break;
        case "RecursiveDivision_Vertical":
            DoRecursiveDivision(0, 0, position.colSize, position.rowSize, walls, true);
            break;
        case "RecursiveDivision_Horizontal":
            DoRecursiveDivision(0, 0, position.colSize, position.rowSize, walls, false);
            break;
        default:
            break;
    }
    return [walls, weights];
}

// 
// horizontal skew
function DoRecursiveDivision(x, y, width, height, walls, boolDirection = null) {
    // +
    // 3
    if (width < 4 || height < 4) {
        return;
    }

    // random
    var curDirection = null;
    if (boolDirection === null) {
        if (width < height) curDirection = direction.horizontal;
        else if (width > height) curDirection = direction.vertical;
        else curDirection = Random(2);
    } else {
        if (boolDirection) {
            curDirection = direction.vertical;
        }
        else {
            curDirection = direction.horizontal;
        }
    }


    var door;
    if (curDirection === direction.horizontal) {
        // height=11x=x0x=10
        //  x 2~8 ()

        // xx0+2random0~6
        var randomX = x + 2 + RandomEven(height - 4);

        // width = 9y=y0y=8
        //  y0+ 1~7random0~6
        door = y + 1 + RandomEven(width - 3);

        //  y0~y0+width
        for (var _y = y; _y < y + width; _y++) {
            if (_y !== door) {
                walls.push([randomX, _y]);
            }
        }

        //  x,y,width  height-x0
        DoRecursiveDivision(x, y, width, randomX - x, walls, boolDirection); // 

        //  x, y, width  height  - ()
        DoRecursiveDivision(randomX, y, width, height - (randomX - x), walls, !boolDirection); // 
    } else {
        // 
        var randomY = y + 2 + RandomEven(width - 4);
        door = x + 1 + RandomEven(height - 3);

        for (var _x = x; _x < x + height; _x++) {
            if (_x !== door) {
                walls.push([_x, randomY]);
            }
        }

        DoRecursiveDivision(x, y, randomY - y, height, walls, boolDirection); // 
        DoRecursiveDivision(x, randomY, width - (randomY - y), height, walls, !boolDirection); // 
    }
}

export default RecursiveDivision;